html {
  background-color: white;
  scroll-behavior: smooth;
  height: 100%;
}
html body .tail-select, html body .tail-select.tail--multiple, html body .tail-select.tail--single {
  border-radius: 8px;
  box-shadow: unset;
  border: 1px solid transparent;
}
html body .tail-select.city-select.tail-default.tail--single:hover {
  border: 1px solid #8c8c8c;
}

body {
  font-family: "Roboto", sans-serif;
  color: #2f2f2f;
  font-style: normal;
}

.container {
  padding: 0 20px;
  width: 1560px;
  margin-inline: auto;
}

.switch-btns {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.switch-btns__btn {
  border-radius: 8px;
  padding-block: 8px;
  padding-inline: 40px;
  background: transparent;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #2f2f2f;
  border: 1px solid #dbdbdb;
  transition: all 0.3s ease;
  cursor: pointer;
}
.switch-btns__btn:hover {
  background-color: #26ad60;
  color: #fff;
  border: 1px solid #26ad60;
}
.switch-btns__btn.active {
  background: #1f422e;
  color: #fff;
  border: 1px solid #1f422e;
  cursor: default;
}

.btn {
  border-radius: 8px;
  padding-block: 12px;
  padding-inline: 40px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  background: #1f422e;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  gap: 10px;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  cursor: pointer;
  text-transform: none;
  text-align: center;
}
.btn:hover {
  background: #26ad60;
}
.btn--transparent {
  background-color: transparent;
  color: #2f2f2f;
  border: 1px solid #26ad60;
}
.btn--transparent:hover {
  background-color: #26ad60;
  color: #fff;
}
.btn--green-light {
  background-color: #26ad60;
  color: #fff;
  border: 1px solid #26ad60;
}
.btn--green-light:hover {
  background-color: #1f422e;
  color: #fff;
  border: 1px solid #1f422e;
}
.btn--white {
  background-color: #fff;
  color: #2f2f2f;
  border: 1px solid #fff;
}
.btn--white:hover {
  background-color: #26ad60;
  color: #fff;
  border: 1px solid #26ad60;
}
.btn--padding-20 {
  padding-inline: 20px;
}
.btn--small {
  padding: 8px 40px;
}
.btn--transparent-hover-white:hover {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
}

.search-module {
  display: flex;
  border-radius: 8px;
  padding-block: 4px;
  padding-inline: 24px 4px;
  width: 300px;
  border: 1px solid #f5f5f5;
  justify-content: space-between;
  align-items: center;
}
.search-module .search__input {
  width: 100%;
}
.search-module .search__input input {
  border: unset;
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #8c8c8c;
}
.search-module .search__btn {
  border-radius: 6px;
  padding: 7px;
  background: #f5f5f5;
  cursor: pointer;
  transition: all 0.3s ease;
}
.search-module .search__btn:hover {
  opacity: 0.5;
}

input[type=checkbox] {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: transparent;
  border: 1px solid #f8f8f8;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  margin: 0;
}
input[type=checkbox]:checked {
  background: transparent;
  border-color: #f8f8f8;
}
input[type=checkbox]:checked::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  background-color: #f8f8f8;
  border-radius: 2px;
}
input[type=checkbox]:hover {
  border-color: rgb(222.5, 222.5, 222.5);
}

a {
  text-decoration: unset;
  transition: all 0.3s ease color;
  color: #2f2f2f;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
}
a[href^=tel] {
  white-space: nowrap;
}

p {
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
}

h1 {
  font-size: 48px;
  line-height: 100%;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  h1 {
    font-size: 32px;
  }
}

.default-separator {
  padding: 12px 0;
}

h2 {
  font-size: 36px;
  line-height: 140%;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  h2 {
    font-size: 28px;
  }
}

h3 {
  font-size: 24px;
  line-height: 140%;
}
@media (max-width: 767px) {
  h3 {
    font-size: 22px;
  }
}

h4 {
  font-size: 18px;
  line-height: 140%;
}
@media (max-width: 767px) {
  h4 {
    font-size: 18px;
  }
}

h5 {
  font-size: 16px;
  line-height: 140%;
}
@media (max-width: 767px) {
  h5 {
    font-size: 16px;
  }
}

:root {
  --option-size: 14px;
  --tail-select--text: #000000;
  --tail-select--meta: #d1d5db;
  --tail-select--muted: #b3b3b3;
  --tail-select--background: #ffffff;
  --tail-select--accent: #3742fa;
  --tail-select--selection: rgba(0, 0, 0, 0.05);
  --tail-select--selection-text: #ffffff;
  --tail-select--selection-muted: rgb(147 197 253 / 1);
}

[data-theme=tail-theme--light] {
  color-scheme: light;
}

[data-theme=tail-theme--dark] {
  color-scheme: dark;
  --tail-select--text: #ffffff;
  --tail-select--meta: #30343b;
  --tail-select--muted: #4d4d4d;
  --tail-select--background: #191b1f;
  --tail-select--accent: #3742fa;
  --tail-select--selection: rgba(0, 0, 0, 0.15);
  --tail-select--selection-text: #ffffff;
  --tail-select--selection-muted: #353b48;
}

.tail-select * {
  box-sizing: border-box;
}

.tail-select,
.tail-select.tail--single,
.tail-select.tail--multiple {
  position: relative;
  display: flex;
  align-items: center;
  user-select: none;
  width: fit-content;
  height: fit-content;
  font-size: 14px;
  line-height: normal;
  font-family: inherit;
  cursor: default;
  color: var(--tail-select--text);
  padding: 6px 6px 6px 6px;
  text-align: left;
  background-color: var(--tail-select--background);
  border: 1px solid var(--tail-select--meta);
  border-radius: 3px;
  box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.05);
  transition: border 0.4s ease-out;
}

.tail-select:hover {
  border-color: var(--tail-select--accent);
}

.tail-select .tail--search,
.tail-select input[type=text].tail--search {
  font-family: inherit;
  color: var(--tail-select--text);
  border: 0 none;
  border-radius: initial;
  border-width: 0;
  border-color: transparent;
  background-color: transparent;
  display: inline-block;
  width: 100%;
  outline: 0;
  font-size: 13px;
  margin: 0;
  padding: 2px 72px 2px 2px;
  cursor: text;
}

.tail-select .tail--search:hover,
.tail-select input[type=text].tail--search:hover {
  border: 0 none;
}

